import { Inject, Injectable, InjectionToken, Optional } from '@angular/core';

export const ConnectExternalAccountsJourneyConfigurationToken =
  new InjectionToken<ConnectExternalAccountsJourneyConfiguration>(
    'ConnectExternalAccountsJourneyConfigurationToken injection token',
  );

export interface ConnectExternalAccountsJourneyConfiguration {
  currencies: string[];
  dismissMessageTime: number;
}

const configDefaults: ConnectExternalAccountsJourneyConfiguration = {
  currencies: ['USD'],
  dismissMessageTime: 3000,
};

@Injectable()
export class ConnectExternalAccountsJourneyConfigService {
  private _config: ConnectExternalAccountsJourneyConfiguration;

  constructor(
    @Optional()
    @Inject(ConnectExternalAccountsJourneyConfigurationToken)
    private readonly config: ConnectExternalAccountsJourneyConfiguration,
  ) {
    this._config = { ...configDefaults, ...(this.config || {}) };
  }

  get defaults(): ConnectExternalAccountsJourneyConfiguration {
    return configDefaults;
  }

  get currencies(): string[] {
    return this._config.currencies;
  }

  get dismissMessageTime(): number {
    return this._config.dismissMessageTime;
  }
}
